import React, { useEffect, useState } from "react";
import axios from "axios";
import "./next-upload.css";

const GET_IMAGES_URL =
  "https://hymqoub9xb.execute-api.ap-south-1.amazonaws.com/v1/render";

const NextUpload = () => {
  const [allImagesWithTypes, setAllImagesWithTypes] = useState("");
  const [currentImageType, setCurrentImageType] = useState("all");

  useEffect(() => {
    const getImages = async () => {
      const response = await axios.get(GET_IMAGES_URL, {
        headers: {
          "Content-Type": "application/json"
        }
      });
      setAllImagesWithTypes(response.data);
    };
    getImages();
  }, []);

  const getImagesWithType = type => {
    return (
      <>
        {allImagesWithTypes &&
          allImagesWithTypes[type] &&
          allImagesWithTypes[type].map((item, index) => {
            return (
              <div className="image-item" key={`image-${index}`}>
                <div className="image">
                  <span className="stain-label">{type}</span>
                  <img src={item} alt="" />
                </div>
                <div className="footer">
                  <span className="img-icon"></span>
                  {`STAIN00203${index}.jpg`}
                </div>
              </div>
            );
          })}
      </>
    );
  };

  return (
    <div
      id="uploadedImages"
      className="uploaded-images-container"
      // style={{display: 'none'}}
    >
      <header className="header">
        <span className="text" onClick={() => setCurrentImageType("all")}>
          All
        </span>
        <span className="text" onClick={() => setCurrentImageType("stain")}>
          Stains
        </span>
        <span className="text" onClick={() => setCurrentImageType("knot")}>
          Knots
        </span>
        <span className="text" onClick={() => setCurrentImageType("hole")}>
          Holes
        </span>
        <span className="text" onClick={() => setCurrentImageType("slub")}>
          Slubs
        </span>
      </header>

      <div className="image-container">
        {(currentImageType === "hole" || currentImageType === "all") &&
          getImagesWithType("hole")}
        {(currentImageType === "stain" || currentImageType === "all") &&
          getImagesWithType("stain")}
        {(currentImageType === "knot" || currentImageType === "all") &&
          getImagesWithType("knot")}
        {(currentImageType === "slub" || currentImageType === "all") &&
          getImagesWithType("slub")}
      </div>
    </div>
  );
};

export default NextUpload;
